    showDropDown() {
      if (this.dropDown) {
        this.closeDropDown();
      } else {
        this.openDropDown();
      }
    },
    closeDropDown() {
      let createDropLinks = this.createDropLinks;
      let dropMenu = document.querySelector(".drop-down__list");
      let cross = document.querySelector(".drop-down__cross");
      let dropBtn = document.querySelector(".drop-down__btn");
      dropMenu.classList.remove("show-dropdown");
      cross.classList.remove("show-cross");
      dropBtn.classList.remove("drop-down__btn--clicked");
      setTimeout(function() {
        dropMenu.classList.remove("show-dropdown-scale");
        createDropLinks();
      }, 600);
      this.dropDown = false;
    },
    openDropDown() {
      let dropMenu = document.querySelector(".drop-down__list");
      let cross = document.querySelector(".drop-down__cross");
      let dropBtn = document.querySelector(".drop-down__btn");
      let closeDropDown = this.closeDropDown;
      cross.onclick = function() {
        cross.classList.remove("show-cross");
        dropBtn.classList.remove("drop-down__btn--clicked");
        closeDropDown();
      };
      dropMenu.classList.add("show-dropdown", "show-dropdown-scale");
      cross.classList.add("show-cross");
      dropBtn.classList.add("drop-down__btn--clicked");
      this.dropDown = true;
    },
    checkIfMobile() {
      if (window.innerWidth < 769) {
        this.screenSize.name = "mob";
      } else if (window.innerWidth < 1440) {
        this.screenSize.name = "mid";
      } else {
        this.screenSize.name = "large";
      }
      if (window.innerWidth > window.innerHeight) {
        this.screenSize.landscape = true;
      } else {
        this.screenSize.landscape = false;
      }
    },
    chooseCat(index, drop) {
      if (drop) {
        this.closeDropDown();
      }
      this.activeMode = 0;
      this.fadeOut();
      let cat = this.photos[index];
      this.photosOnPage = [];

      cat.albums.forEach(album => {
        let pack = {
          album: album,
          url: album.url + "1"
        };
        this.photosOnPage.push(pack);
      });

      this.rows = [];
      this.makeRows();
      this.activeCat = index;

      this.breadcrumbs.zero = {
        name: "home / "
      };

      this.breadcrumbs.one = {
        name: this.photos[index].category,
        index: index
      };
      this.breadcrumbs.two = { name: " / all", index: null };

      for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
        pic.classList.remove("clicked");
      }
      document.querySelector("#more-btn").style.display = "none";
    },

    addClicked(e) {
      if (this.screenSize.name !== "mob") {
        let el;

        if (e.target.parentElement.classList[0] === "galery__img-wrapper") {
          el = e.target.parentElement;
        } else {
          el = e.target.parentElement.parentElement;
        }

        if (!el.classList.contains("clicked")) {
          for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
            pic.classList.remove("clicked");
          }

          el.classList.add("clicked");
        }
      }
    },

    chooseAlbum(photo, e) {
      let el;

      this.activeCat = photo.album.catIndex - 1;

      if (e.target.parentElement.classList[0] === "galery__img-wrapper") {
        el = e.target.parentElement;
      } else {
        el = e.target.parentElement.parentElement;
      }

      if (el.classList.contains("clicked")) {
        this.fadeOut();
        if (this.activeMode === 0) {
          this.photosOnPage = [];

          let cat = this.photos[photo.album.catIndex - 1];

          let album = cat.albums[photo.album.albumIndex];

          for (let i = 0; i < album.length; i++) {
            let pack = {
              album: album,
              url: album.url + (i + 1 + ""),
              index: i,
              orientation: album.orientation[i]
            };

            this.photosOnPage.push(pack);
          }

          this.rows = [];
          this.makeRows();
          this.activeMode = 1;

          this.breadcrumbs.zero = {
            name: "home / "
          };

          this.breadcrumbs.one = {
            name: this.photos[photo.album.catIndex - 1].category,
            index: photo.album.catIndex - 1
          };
          this.breadcrumbs.two = {
            name: " / " + photo.album.name,
            index: null
          };

          for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
            pic.classList.remove("clicked");
          }
          document.querySelector("#more-btn").style.display = "none";
        } else {
          if (this.screenSize.name !== "mob") {
            this.$refs.slider.$emit("slideTo", photo.index);
            setTimeout(this.openSingleView, 250);
          } else {
            this.openSingleView();
          }
        }
      } else {
        for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
          pic.classList.remove("clicked");
        }

        el.classList.add("clicked");
        if (this.activeMode === 1 && this.screenSize.name === "mob") {
          this.$refs.slider.$emit("slideTo", photo.index);
          // this.options.currentPage = photo.index;
        }
      }
    },
    openSingleView() {
      document.querySelector(".agile").classList.add("agile-show");
      document
        .querySelector(".galery__zoomed-close")
        .classList.add("galery__zoomed-close-show");
      document.querySelector(".page-cover").classList.add("page-cover-show");
    },
    fadeOut() {
      if (this.activeMode === 0) {
        for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
          pic.classList.add("fade-out");
        }
      }
    },
    loadAll(clear) {
      this.activeMode = 0;
      this.photosOnPage = [];
      if (clear === true || this.clearGalery) {
        console.log("clear: ", clear);

        this.clearGalery = false;
        this.photosOnPage = [];
        this.rows = [];
        this.breadcrumbs.zero = {
          name: ""
        };
        this.breadcrumbs.one = {
          name: "home /",
          index: -1
        };
        this.breadcrumbs.two = { name: "", index: null };
      }
      if (this.activeMode === 0) {
        this.shufleNumbers();

        let index = 0;

        let cat;

        let randomAlbum = 0;

        let albumsQuantity;

        let album;

        for (let i = 0; i < 9; i++) {
          if (index === 7) {
            index = 0;
          }

          let url = "";

          cat = this.photos[this.order[index]];

          albumsQuantity = cat.albums.length;

          randomAlbum = this.getRndInteger(0, albumsQuantity);

          album = cat.albums[randomAlbum];

          let photosQuantity = album.length;

          let randomPhoto = this.getRndInteger(1, photosQuantity + 1);

          url += album.url + randomPhoto;

          let photo = {
            url: url,
            album: album
          };

          this.photosOnPage.push(photo);

          index++;
        }

        this.makeRows();
        this.activeCat = -1;
        this.addPhoto = 3;
        setTimeout(this.compareRows, 2000);
        document.querySelector("#more-btn").style.display = "block";
      }
    },
    compareRows() {
      console.log(document.querySelector(".galery").children);
      let galery = document.querySelector(".galery").children;
      let index = 1;
      let longest = {
        height: 0,
        index: null
      };
      let shortest = {
        height: 10000,
        index: null
      };
      for (let row of galery) {
        let rowHeight = 0;
        if (row.children.length) {
          for (let photo of row.children) {
            rowHeight += photo.scrollHeight;
          }
          console.log("rowHeight: ", rowHeight);
          if (rowHeight > longest.height) {
            longest.index = index;
            longest.height = rowHeight;
          }
          if (rowHeight < shortest.height) {
            shortest.height = rowHeight;
            shortest.index = index;
          }
        }
        index++;
      }
      let dif = longest.height - shortest.height;
      if (dif > 380) {
        this.addOnePhoto(shortest.index);
      }
      console.log(this.screenSize);

      if (window.innerWidth < 450 && dif > 200) {
        this.addOnePhoto(shortest.index);
      }
      console.log("difference: ", dif);

      console.log("shortest: ", shortest);
      console.log("longest: ", longest);
    },
    addOnePhoto(shortest) {
      console.log("add new photo!!!", shortest);
      let randomCat = this.getRndInteger(0, 6);
      let category = this.photos[randomCat];

      let albumIndex = this.getRndInteger(0, category.albums.length);
      let url = category.albums[albumIndex].url;
      let photoIndex = this.getRndInteger(
        1,
        category.albums[albumIndex].length
      );
      console.log("photo index: ", photoIndex);
      url += photoIndex;
      let pack = {
        album: category.albums[albumIndex],
        url: url
      };

      console.log("length before: ", this.rows[shortest - 1].length);

      this.rows[shortest - 1].push(pack);

      console.log("length after: ", this.rows[shortest - 1].length);

      this.photosOnPage.push(pack);
      console.log("row", this.rows[shortest - 1]);
      this.addPhoto -= 1;
      setTimeout(this.fadeIn, 500);
      if (this.addPhoto >= 0) {
        setTimeout(this.compareRows, 1000);
      }
    },
    makeRows() {
      let rowQuantity = 3;
      if (this.screenSize.name === "mob") {
        rowQuantity = 2;
      } else if (this.screenSize.name === "mid") {
        rowQuantity = 3;
      }
      // else {
      //   rowQuantity = 4;
      // }

      for (let i = 0; i < rowQuantity; i++) {
        this.rows.push([]);
      }

      let row = 0;
      for (let i = 0; i < this.photosOnPage.length; i++) {
        if (row === rowQuantity) {
          row = 0;
        }
        this.rows[row].push(this.photosOnPage[i]);

        row++;
      }

      setTimeout(this.fadeIn, 1000);
    },
    fadeIn() {
      for (let pic of document.querySelectorAll(".galery__img-wrapper")) {
        pic.classList.remove("fade-out");
        pic.classList.add("fade-in");
      }
    },
    sliderArrow(dir) {
      if (dir === "left") {
        this.$refs.slider.$emit("slidePre");
      } else {
        this.$refs.slider.$emit("slideNext");
      }
    },
    testFunc() {
      console.log("test");
    },

    shufleNumbers() {
      this.shufleCategories(this.order);
    },

    closeZoomedImage() {
      document.querySelector(".agile").classList.remove("agile-show");
      document
        .querySelector(".galery__zoomed-close")
        .classList.remove("galery__zoomed-close-show");
      document.querySelector(".page-cover").classList.remove("page-cover-show");
    },

    shufleCategories(arr) {
      let newArr = arr;
      let currentIndex = newArr.length;
      let temporaryValue, randomIndex;

      while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        temporaryValue = newArr[currentIndex];
        newArr[currentIndex] = newArr[randomIndex];
        newArr[randomIndex] = temporaryValue;
      }
      let randomizedArray = newArr;
      return randomizedArray;
    },
    getRndInteger(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },
    resize() {
      let func = this.checkIfMobile;
      window.onresize = function() {
        func();
      };
    }
  },

  mounted() {
    // this.testFunc();
    this.activeMode = 0;

    this.checkIfMobile();

    this.createDropLinks();

    this.resize();

    this.loadAll();
  }
};
</script>

